.options {
  background-color: #424242;
  width: 150px;
  position: absolute;
  top: 12px;
  right: 36px;
  z-index: 10;
  -webkit-animation: appear 0.3s ease;
          animation: appear 0.3s ease;
  box-shadow: 0 0 3px #3e3e3e; }

.options button {
  background-color: transparent;
  padding: 8px 25px;
  display: block;
  width: 100%;
  text-align: left; }

.options li {
  border-bottom: solid 1px #3d3d3d;
  border-top: solid 1px #3d3d3d;
  -webkit-transition: background-color 0.3s, border-bottom-color 0.3s, border-top-color 0.3s;
  transition: background-color 0.3s, border-bottom-color 0.3s, border-top-color 0.3s; }
  .options li:hover {
    background-color: #383838;
    border-bottom-color: #383838;
    border-top-color: #383838; }

.layer {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 9; }

@-webkit-keyframes appear {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes appear {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
